import React, {Component} from 'react';

class ArticleItem extends Component  {

  constructor(props){
    super(props);
    this.state = {
    }
  }

  render(){
    return (
      <main className="article-wrapper">
        <div className="container">
          <ul className="article-list">
            <div className="list-group-item list-group-item-action">
              <div className="row ">
                <h5 className="col-10 ">{this.props.article.title}</h5>
                <h5 className="col-2">${this.props.article.author}</h5>
              </div>
                <p className="col-md-auto mb-1"> {this.props.body}</p>
                <button type="button" className="btn btn-success" onClick={()=>this.props.readMore(this.props.article)}>Read More</button>
            </div>
          </ul>
        </div>
    </main>

    );
  }
}

class ArticleList extends Component{

  render(){
  const headliners = this.props.articles.filter(article => article.headliner).map(article => <Headliners key={article.id} article={article} readMore={props.readMore} />);
  const feature = props.articles.filter(article => !article.headliner).map(article => <Feature key={article.id} article={article} readMore={props.readMore} />);
  return(
    <div className="row mt-3 mr-5 ml-5 no-gutters d-flex justify-content-around">
      <div className="col-7">
        <h3 className="category-header">Headliners</h3>
        <div className="list-group">
        {headliners}
        </div>
      </div>
      <div className="col-3">
        <h3 className="category-header">Feature</h3>
        {feature}
      </div>
    </div>
    );
  }
}
export default ArticleList;
