  import React, { Component } from 'react';
  import './index.css';
  import Cookies from 'js-cookie';
  import ArticleForm from './ArticleForm';
  import Editorials from './Editorials';
  import Entertainment from './Entertainment';
  import Login from './Login';
  import ArticleList from './ArticleList';
  import Register from './Register';
  import Sport from './Sport';

  class App extends Component {
    constructor(props) {
      super(props)

      this.state = {
        articles: [],
        title: [],
        loggedin: Cookies.get('Authorization')

}
      this.fetchArticles = this.fetchArticles.bind(this);
      this.handlePost = this.handlePost.bind(this);
      this.registration = this.registration.bind(this);
      this.logIn = this.logIn.bind(this);
      this.logOut = this.logOut.bind(this);
    }

    componentDidMount(){
      this.fetchArticles();
    }

    fetchArticles(){
      const logIn = this.state.logIn;
      if (logIn === true){
        fetch ('api/v1/articles/')
        .then (response => response.json())
        .then (data => this.setState({articles: data}))
        .catch (error => console.log ('Error:', error));
      }
    }

    async logIn(e, obj){
        e.preventDefault();

        const options = {
          method: 'POST',
          headers: {
            'X-CSRFToken': Cookies.get('csrftoken'),
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(obj)
        };

        const handleError = (err) => console.warn(err);
        const response = await fetch('api/v1/rest-auth/login/', options)
        const data = await response.json().catch(handleError)

      if(data.key){
        Cookies.set('Authorization', `Token ${data.key}`);
        }
      }

    async handlePost(e, obj){
        e.preventDefault();
        const options = {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-CSRFToken': Cookies.get('csrftoken'),
           },
           body: JSON.stringify(obj)
         };

      const handleError = (err) => console.warn(err);
      const response = await fetch('/api/v1/articles/', options);
      const data = await response.json().catch(handleError);

      if(data.key){
      Cookies.set('Authorization', `Token ${data.key}`)
         }

       }
       async registeration(e, obj){
           e.preventDefault();

           const options = {
             method: 'POST',
             headers: {
               'X-CSRFToken': Cookies.get('csrftoken'),
               'Content-Type': 'application/json'
             },
             body: JSON.stringify(obj),
           };

           const handleError = (err) => console.warn(err);
           const response = await fetch('api/v1/rest-auth/registration/', options)
           const data = await response.json().catch(handleError)

           if(data.key){
             Cookies.set('Authorization', `Token ${data.key}`);
           }
         }

         async logOut(e){
                e.preventDefault();

                const options = {
                  method: 'POST',
                  headers: {
                    'Content-Type': 'application/json',
                    'X-CSRFToken': Cookies.get('csrftoken'),
                  },
                };

                const handleError = (err) => console.warn(err);
                const responce = await fetch('/api/v1/rest-auth/logout/', options);
                const data = await response.json().catch(handleError);

                if(data.detail === "Successfully logged out."){
                  Cookies.remove('Authorization');
                  this.setState({page: 'login'});
                }

              }

              render(){
                console.log(this.state.loggedIn);
                let html;
                const display = this.state.display;
                if (display === 'register'){
                  html = <div className='form'><Register registerUser={this.registerUser}/></div>
                } else if (display === 'login') {
                  html = <div className='form'><Login logIn={this.logIn}/></div>
                }

                let article;
                const logIn  = this.state.logIn;
                if (logIn === true){
                  article = <div className='ArticleForm'><ArticleForm articless={this.state.articless} handlePost={this.handlePost} postArticle={this.postArticle} logOut={this.logOut}/></div>
                } else {
                  article= ''
                }
                  return(
                    <React.Fragment>
                    {logIn === false?<div className="mt-6">
                      {html}
                      <div className="form">
                        <button onClick={() => this.setState({display:'login'})} type='button'className="btn btn-link">Already have an account?</button>
                        <button onClick={() => this.setState({display:'register'})} type='button'className="btn btn-link">Don't have an account?</button>
                      </div>
                    </div>
                    : <div>{article}</div>
                    }
                    </React.Fragment>
                  );
                  }
                }


              export default App;
